import { useEffect, useState } from "react";
import {
  Container,
  Grid,
  Card,
  CardContent,
  Typography,
  Divider,
  Box,
} from "@mui/material";
import { bankPublic } from "../../../api/login/Login";

function CompanyBankDetails() {
  const [data, setData] = useState([]);

  const detailsBank = async () => {
    try {
      const res = await bankPublic();
      setData(res?.data?.data || []);
    } catch (error) {
      console.error("Error fetching bank details", error);
    }
  };

  useEffect(() => {
    detailsBank();
  }, []);

  return (
    <Container
      sx={{
        width: "100vw",
        minHeight: "100vh",
        backgroundColor: "var(--bs-primary1)",
        py: 4,
      }}
    >
      <Typography
        variant="h4"
        gutterBottom
        sx={{
          fontWeight: "bold",
          color: "var(--bs-primary2)",
          textAlign: "center",
          mb: 4,
        }}
      >
        Company Bank Details
      </Typography>

      <Grid container spacing={4}>
        {data.map((item, index) => (
          <Grid item xs={12} sm={6} md={4} key={index}>
            <Card
              sx={{
                backgroundColor: "var(--bs-primary3)",
                borderRadius: 3,
                boxShadow: 3,
                transition: "0.3s",
                "&:hover": {
                  transform: "scale(1.05)",
                  boxShadow: 6,
                },
              }}
            >
              <CardContent>
                <Box sx={{ textAlign: "center", mb: 2 }}>
                  <Typography
                    variant="h6"
                    sx={{ fontWeight: "bold", color: "var(--bs-primary2)" }}
                  >
                    {item.bank_name}
                  </Typography>
                  <Divider sx={{ my: 1, backgroundColor: "var(--bs-primary2)" }} />
                </Box>

                <Typography
                  variant="body1"
                  sx={{ fontSize: "1rem", fontWeight: 500, color: "var(--bs-primary2)" }}
                >
                  <strong>Account Number:</strong> {item.bank_account_number}
                </Typography>
                <Typography
                  variant="body1"
                  sx={{ fontSize: "1rem", fontWeight: 500, color: "var(--bs-primary2)" }}
                >
                  <strong>IFSC Code:</strong> {item.ifsc_code}
                </Typography>
                <Typography
                  variant="body1"
                  sx={{ fontSize: "1rem", fontWeight: 500, color: "var(--bs-primary2)" }}
                >
                  <strong>Bank Code:</strong> {item.bank_code}
                </Typography>
                <Typography
                  variant="body1"
                  sx={{ fontSize: "1rem", fontWeight: 500, color: "var(--bs-primary2)" }}
                >
                  <strong>Branch Name:</strong> {item.branch_name}
                </Typography>

                {item.remark && (
                  <Typography
                    variant="body2"
                    sx={{
                      mt: 2,
                      fontStyle: "italic",
                      color: "var(--bs-primary2)",
                      fontWeight: 500,
                    }}
                  >
                    <strong>Remarks:</strong> {item.remark}
                  </Typography>
                )}
              </CardContent>
            </Card>
          </Grid>
        ))}
      </Grid>
    </Container>
  );
}

export default CompanyBankDetails;
